.div_card
{
    width: 100%;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15); 
    position: relative;
}

.div_card > h4
{
    font-size: 1.4em;
    font-weight: 600;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: var(--white);
    font-family: "Roberto", sans-serif;
}

