.pic_container
{
    width: inherit;
    height: 100%; /* 10rem*/
    position: relative;
}

.pic_container > img
{
    width: 100%;
    height: 100%;
}

.pic_container::before
{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right bottom, rgba(3,29,58, 0.7), rgba(8,40,129, 0.7));
    visibility: transparent;
}

.btn_card
{
    padding: 0.8rem 2.6rem;
    display: block;
    border: 1px var(--bg-main) solid;
    cursor: pointer;
    background-color: var(--white);
    margin: 0 auto 0 auto;
    position: absolute;
    top: 80%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.btn_card:hover
{
    background-color: var(--bg-main);
    color: var(--white);
}



.section_box
{
  font-family: "Poppins", sans-serif;
  /* font-size: 1.3rem; */
  color: #0c2340;
  width: 90%; 
  margin: 0 auto;
  text-align: center;
}

.section_box > h2
{
  display: inline-block;
  -webkit-transform: translateY(1.5rem);
          transform: translateY(1.5rem);
  background-color: #f6f6f7;
  padding: 0 0.6rem;
}

.section_box > hr
{
  width: 70%;
