.navbar_menu-main
{
    position: absolute;
    right: 8%;
    font-size: 1.17rem; 
    background-color: var(--white);
    width: 824px;
    float: right;
    align-self: center;
    z-index: 10;
    box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.123);
}

.navbar_menu-main a
{
    color: var(--bg-main);
}

.navbar_menu-main ul,
.navbar_menu-sec ul
{
    display: flex;
    justify-content: space-between;
}

.navbar_menu-main ul li,
.navbar_menu-sec ul li
{
    padding: 1.3rem 1.95rem;
}

.navbar_menu-main li
{
   /* transition: width 2s; */
    position: relative;  
}

.navbar_menu-main li:hover > div
{
    display: block;
}

.navbar_menu-main li::after,
.navbar_menu-main li::before
{
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    bottom: 0; 
    background: var(--ash);
    transition: width 0.8s; 
}

.navbar_menu-main li:hover::after,
.navbar_menu-main li:hover::before
{
    width: 40%;
    height: 4px;
}

.navbar_menu-main li::after
{
    left: 50%; 
}

.navbar_menu-main li::before
{
    right: 50%;  
}

