/* dropdown menu */
.drop-menu
{
    content: "";
    min-width: 180px;
    display: none;
    position: absolute;
    top: 90%;
    left: 0%;
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
    background-color: white;
    text-align: center;
    border-radius: 6px; /*12px*/ 
    box-shadow: 2px 3px 5px rgb(0, 0, 0, 0.5);
    overflow: hidden;
}

.drop-menu ul li
{
    display: block;
    padding: 5px 0;
    color: var(--bg-main); 
}

.drop-menu ul li:hover
{
    color: white;
    background-color: #878c96;/* rgb(100, 18, 58); */
}

.drop-menu:hover
{
    display: block;
}

